<template>
    <div>
        <a-table :dataSource="data" :pagination="false" rowKey ="id">
            <a-table-column title="id" dataIndex="id" />
            <a-table-column title="" dataIndex="position_id" />
            <a-table-column title="" dataIndex="title" />
            <a-table-column title="" dataIndex="picture" >
                <template slot-scope="picture" v-if="picture">
                    <img :src="picture" alt="" style="width: 50px ;height: 50px">
                </template>
            </a-table-column>
            <a-table-column title="" dataIndex="link_type" />
            <a-table-column title="" dataIndex="link_target" />

            <a-table-column title="" dataIndex="created_at" />
            <a-table-column title="" dataIndex="updated_at" />

            <a-table-column title=""  dataIndex="action">
                <template slot-scope="text, record">
            <span>
              <a-button type="primary" @click="update(record.id)"></a-button>
              <a-popconfirm  title="?" @confirm="() => onDelete(record.id)" >
                <a-icon slot="icon" type="question-circle-o" style="color: red"/>
                <a-button type="danger"></a-button>
              </a-popconfirm>
        </span>
                </template>
            </a-table-column>
        </a-table>
        <a-pagination @change="pageChange"  :pageSize="pageSize" :current="page" :total="total" />
    </div>
</template>

<script>
    import qs from "qs";
    import api from '../../api'
    const link_type_list = ['', '', '', ''];
    const position =['', 'banner', 'icon', '4',];
    export default {
        data() {
            return {
                data: [],
                link_type_list,
                position,
                page:1,
                pageSize:2,
                total:null,
            };
        },
        methods: {
            getInfo(page,pageSize) {
                this.axios
                    .get(api.NavGet,{
                        params:{
                            'page':page,
                            'perpage':pageSize,
                        }
                    })
                    .then(response => {
                        console.log(response);
                        this.total = response.data.data.total;
                        response.data.data.data.forEach((val, key) => {
                            this.data.push(
                                {
                                    id: val.id,
                                    position_id: this.position[val.position_id-1],
                                    title: val.title,
                                    picture: val.picture,
                                    link_type: this.link_type_list[val.link_type-1],
                                    link_target: val.link_target,
                                    // status: val.status,
                                    created_at: val.created_at,
                                    updated_at: val.updated_at
                                },
                            );
                        })

                    }).catch(err => {
                    console.log(err);
                })
            },

            onDelete(key) {
                    let data ={
                        'id':key,
                    }
                    this.axios.post(api.NavDel,qs.stringify(data)).then(res=>{
                       if(res.data.status){
                           this.$message.success(res.data.msg, 3);
                           const dataSource = [...this.data];
                           this.data = dataSource.filter(item => item.id !== key);
                       }else{
                           this.$message.error(res.data.msg, 3);
                       }
                        console.log(response);
                    }).catch(err=>{
                        console.log(err);
                    })

            },
            update(id) {
                console.log(id);
                this.$router.push({name: 'update_nav', query: {id: id}});
            },
            pageChange(current) {
                this.page = current;
                this.data =[];
                this.getInfo(this.page,this.pageSize);
            },
        },
        created() {
            this.getInfo(this.page,this.pageSize);
        }
    }
</script>
<style scoped lang="scss">
    .ant-pagination{
        float:right;
        margin-top:10px !important;
    }
</style>
