<template>
    <a-form :form="form" @submit="handleSubmit">
        <a-form-item label="" v-bind="formItemLayout">
            <a-input  placeholder=""
                      v-decorator="['nav_title',{ rules: [{ required: true, message: '' }]},]"
            />
        </a-form-item>
        <a-form-item  v-bind="formItemLayout" label="" has-feedback >
            <a-select style="width: 100%"
                      v-decorator="['position_id',{ rules: [{ required: true, message: '' }],initialValue:position_id},]"
            >
                <a-select-option :value="index+1"  v-for="(posName,index) in position" :key="index">{{posName}}</a-select-option>
            </a-select>
        </a-form-item>
        <!---->
        <a-form-item label="" v-bind="formItemLayout">
            <a-upload
                    listType="picture-card"
                    :fileList="fileList"
                    @preview="handlePreview"
                    @change="imageChange"
                    :customRequest="customRequest"
            >
                <div v-if="fileList.length < 1">
                    <a-icon type="plus"/>
                    <div class="ant-upload-text"></div>
                </div>
            </a-upload>
            <a-modal :visible="previewVisible" :footer="null" @cancel="handleCancel">
                <img alt="example" style="width: 100%" :src="previewImage"/>
            </a-modal>
        </a-form-item>

        <!---->
        <a-form-item   v-bind="formItemLayout" label="" has-feedback>
            <a-select  style="width: 100%" @change="handleProvinceChange"
                      v-decorator="['link_type',{ rules: [{ required: true, message: '' }],initialValue:1},]"
            >
                <a-select-option  v-for="(province,index) in link_type_list" :key="province" :value="index+1"
                >
                    {{province}}
                </a-select-option
                >
            </a-select>
        </a-form-item>

        <a-form-item v-bind="formItemLayout" label="" has-feedback v-if="link_target_list">
            <a-select  style="width: 100%"
                       v-decorator="['link_target',{ rules: [{ required: true, message: '' }],initialValue:1},]"
            >
                <a-select-option  v-for="(target,index) in link_target_list" :value="target.id" :key="index">
                    {{target.name}}</a-select-option>
            </a-select>
        </a-form-item>
        <a-form-item :wrapper-col="{ span: 12, offset: 6 }">
            <a-button type="primary" html-type="submit">
                
            </a-button>
        </a-form-item>
    </a-form>
</template>
<script>
    import qs from "qs";
    import api from '../../api';

    const link_type_list = ['', '', '', ''];
    const position =['', 'banner', 'icon', '4',];
    export default {
        data() {
            return {
                previewVisible: false,
                previewImage: '',
                form: this.$form.createForm(this, {name: 'dynamic_rule'}),
                formItemLayout: {
                    labelCol: {span: 6},
                    wrapperCol: {span: 10},
                },
                fileList: [],
                link_type_list,
                link_target_list:[],
                link:[],
                position,
                data: [],
                position_id:1,
                link_type: 1,
                link_target: '',
            };
        },
        methods: {
            getCate() {
                // 
                return new Promise((resolve, reject) => {
                    this.axios.get(api.CateGet).then(res => {
                        resolve(
                            this.link[1] = res.data.data.data,
                            this.link_target_list = this.link[1],
                        );
                    }).catch(err => {
                        reject(console.log(err));
                    })
                })
            },
            getProduct() {
                //
                return new Promise((resolve, reject) => {
                    this.axios.get(api.Product).then(res => {
                        console.log(res,'');
                        resolve(this.link[2] = res.data.data.data);
                        console.log(this.link[this.link_type].length);
                    }).catch(err => {
                        reject(console.log(err));
                    })
                })
            },
            handleCancel() {
                this.previewVisible = false;
            },
            customRequest(file) {
                const formData = new FormData();
                formData.append("imageFile", file.file);
                this.toUpload(formData);
            },
            handlePreview(file) {
                this.previewImage = file.url || file.thumbUrl;
                this.previewVisible = true;
            },
            imageChange({fileList}) {
                this.fileList = fileList;
                // console.log(fileList);
            },
            toUpload(file) {
                let config = {
                    headers: {"Content-Type":"multipart/form-data"}
                }; //
                this.axios
                    .post(api.Upload, file, config)
                    .then(res => {
                        if (res.data.status) {
                            console.log(res.data.data);
                            this.fileList = [];
                            this.fileList.push({
                                uid:-1,
                                status : "done",
                                url:res.data.data.path,
                                fileKey:res.data.data.fileKey,
                            })
                        } else {
                            this.$message.error("");
                        }
                    });
            },
            handleProvinceChange(value) {
                console.log(value);

                this.link_type = value;
                this.link_target = 1;
                this.link_target_list = this.link[value];
            },
            handleSubmit(e) {
                e.preventDefault();
                this.form.validateFields((err, values) => {
                    if (!err) {
                        console.log('Received values of form: ', values);
                        let data={
                            'position_id':values.position_id,
                            'title':values.nav_title,
                            'picture':this.fileList.length>0?this.fileList[0].fileKey:'',
                            'link_type':values.link_type,
                            'link_target':this.link_target
                        }
                        this.axios.post(api.NavAdd,qs.stringify(data)).then(res=>{
                            if(res.data.status){
                                this.$router.push('/table_nav');
                            }
                        }).catch(err=>{
                            console.log(err);
                        })
                    }
                });
            },

        },
        created() {
            this.getCate()
                .then(() => {
                    this.getProduct();
                })

        }
    }
</script>
